:root {
    --white: white;
    --navbackground: #01010d;
    --background: #01010d;
    --fade-one: #a7adba;
    --fade-two: #65737e;
    --fade-three: #343d46;
    --fade-four: #343d46;

    --font: 'Urbanist', sans-serif;
}

.nav-item,
i, a {
    color: white;
    color: var(--white);
    transition: 0.2s ease-in-out;
}

.nav-item {
    font-family: 'Urbanist', sans-serif;
    font-family: var(--font);
}

.navbar-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 7vh;
    max-width: 100vw;
    padding: 0 1rem;
    background: #01010d;
    background: var(--navbackground);
}

.navbar-left {
    display: flex;
    flex-direction: row;
}

.bell-wrapper {
    margin-right: 1rem;
}

.social-icons > a {
    margin: 0 0.3rem;
}

.social-icons.active {
    display: flex;
    flex-direction: row;
}

#media-show {
    display: flex;
}

.media-hide {
    opacity: 0;
}

.media-fade-in {
	opacity: 1;
	-webkit-animation-name: fadeInOpacity;
	        animation-name: fadeInOpacity;
	-webkit-animation-iteration-count: 1;
	        animation-iteration-count: 1;
	-webkit-animation-timing-function: ease-in;
	        animation-timing-function: ease-in;
	-webkit-animation-duration: 2s;
	        animation-duration: 2s;
}

.social-icons {
    display: none;
}

.navbar-right {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.navbar-right > a {
    margin: 0 0.5rem;
    text-decoration: none;
    font-size: 150%;

    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.hamburger {
    display: none;
}

.bar {
    display: block;
    width: 25px;
    height: 3px;
    margin: 5px auto;
    transition: all 0.3s ease-in-out;
    background: white;
    background: var(--white);
}

/* ANIMATION */
.btn-hvr:hover,
.btn-hvr:focus {
    color: #343d46;
    color: var(--fade-four);
    border: none;
}

.icon-hvr:hover,
.icon-hvr:focus {
    opacity: 0.25;
    border: none;
}

@-webkit-keyframes fadeInOpacity {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@keyframes fadeInOpacity {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
:root {
    --white: white;
    --navbackground: #01010d;
    --background: #01010d;
    --fade-one: #a7adba;
    --fade-two: #65737e;
    --fade-three: #343d46;
    --fade-four: #343d46;
    --mobile-nav: #060121;
    --mobile-shadow: #1E2832;

    --font: 'Urbanist', sans-serif;
}

@media all and (max-width: 768px) {
    .navbar-right {
        flex-direction: column;
        position: fixed;
        top: 3rem;
        padding: 1.5rem 0;
        background: #060121;
        background: var(--mobile-nav);
        border: 2px solid white;
        border: 2px solid var(--white);
        border-radius: 10px;
        box-shadow: 5px 5px 0px #1E2832;
        box-shadow: 5px 5px 0px var(--mobile-shadow);
        right: -100%;
        transition: 0.3s;
        z-index: 1;
    }

    .navbar-right.active {
        right: 2%;
    }

    .hamburger {
        display: block;
    }

    .social-icons {
        position: absolute;
        display: none;
        flex-direction: column;
        top: 7vh;
        left: 0.6rem;
    }

    .social-icons.active {
        display: flex;
        flex-direction: column;
    }

    .hamburger.active .bar:nth-child(2) {
        opacity: 0;
    }

    .hamburger.active .bar:nth-child(1) {
        -webkit-transform: translateY(8px) rotate(45deg);
                transform: translateY(8px) rotate(45deg);
    }

    .hamburger.active .bar:nth-child(3) {
        -webkit-transform: translateY(-8px) rotate(-45deg);
                transform: translateY(-8px) rotate(-45deg);
    }
}

@media all and (max-width: 556px) {
    .social-icons {
        font-size: 85%;
    }
}

@media all and (max-width: 420px) {
    .social-icons {
        font-size: 75%;
    }
}

