:root {
    --white: white;
    --navbackground: #01010d;
    --background: #01010d;
    --fade-one: #a7adba;
    --fade-two: #65737e;
    --fade-three: #343d46;
    --fade-four: #343d46;

    --font: 'Urbanist', sans-serif;
}

.about-wrapper {
    background: #01010d;
    background: var(--background);
    height: 88vh;
    width: 100vw;
}

.about-top {
    position: relative;
    height: 18vh;
    padding: 0 1rem;
    color: white;
    color: var(--white);
    font-family: 'Urbanist', sans-serif;
    font-family: var(--font);
    font-size: 175%;
}

.about-header {
    position: absolute;
    letter-spacing: .2rem;
    bottom: 0;

    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.about-arrow-down {
    position: absolute;
    bottom: 0;
    right: 3rem;

    -webkit-animation: fadeInOut ease 2s;

            animation: fadeInOut ease 2s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

.about-bottom {
    position: relative;
    height: 70vh;
}

.about-sect {
    position: absolute;
    width: 40%;
    height: 60%;
    z-index: 0;
}

.about-sect-header {
    position: relative;
    padding: 0.7rem;
    width: 80%;
    background: #343d46;
    background: var(--fade-three);
    z-index: 2;

    font-family: 'Urbanist', sans-serif;

    font-family: var(--font);
    font-size: 150%;
    color: white;
    color: var(--white);
    box-shadow: 0px 3px 6px 0px #01010d;
    box-shadow: 0px 3px 6px 0px var(--background);
}

.about-sect-header-one {
    border-radius: 15px 15px 15px 0;
    float: right;
}

.about-sect-header-two {
    border-radius: 15px 15px 0 15px;
}

.about-sect-one {
    top: 3rem;
    left: 3rem;
}

.about-sect-two {
    bottom: 3rem;
    right: 3rem;
}

.about-sect-info {
    position: absolute;
    height: 90%;
    width: 90%;
    background: white;
    background: var(--white);
    border-radius: 15px;

    z-index: 1;
}

.about-sect-info-one {
    bottom: 0;
    left: 0;
    box-shadow: -3px 4px 6px 5px #343d46;
    box-shadow: -3px 4px 6px 5px var(--fade-four);
}

.about-sect-info-two {
    bottom: 0;
    right: 0;
    box-shadow: 3px 4px 6px 5px #343d46;
    box-shadow: 3px 4px 6px 5px var(--fade-four);
}

.about-sect-text {
    padding: 2.5rem 1.5rem 1rem 1.5rem;
    font-size: 150%;
    color: black;
}

@-webkit-keyframes fadeInOut {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@keyframes fadeInOut {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
